<template>
  <div class="hello">
    <div style="height: 40px;width: 100%;background-color: #C20C0C;text-align: center;position: fixed;z-index: 9;">
<!--      <img src="../../static/img/format.png" class="autImg" style="cursor: pointer;">-->
      <span class="text">Cloudmusic</span>
    </div>
    <div class="nevaBar">
      <div @click="nevaBarId = 1"><span :class="nevaBarId === 1?'action':''">推荐歌单</span></div>
      <div @click="nevaBarId = 2"><span :class="nevaBarId === 2?'action':''">热歌榜</span></div>
      <div @click="nevaBarId = 3"><span :class="nevaBarId === 3?'action':''">搜索</span></div>
    </div>
    <div class="nevaBarPage" v-show="nevaBarId === 1">
<!--      推荐歌单<i class="iconfont big-sousuo3"></i>-->
      <div style="height: 20px;line-height: 20px;padding: 20px 0;">
        <span style="border-left: 2px solid #C20C0C;padding: 2px 0 2px 10px;font-size: 16px;font-weight: 600;">推荐歌单</span>
      </div>
<!--      歌单列表-->
      <div class="ream-ul">
        <div class="ream-li" v-for="(item, index) in playList" :key="index" :style="index%3 === 0?'padding-left:0;':''" v-if="index < 3">
          <div>
            <img :src="item.picUrl" alt="">
            <span><i class="iconfont big-icon-test15"></i>{{(item.playCount/10000).toFixed(1)}}万</span>
          </div>
          <span class="remd_text">{{item.name}}</span>
        </div>
      </div>
      <div class="ream-ul">
        <div class="ream-li" v-for="(item, index) in playList" :key="index" :style="index%3 === 0?'padding-left:0;':''" v-if="index >= 3">
          <div>
            <img :src="item.picUrl" alt="">
            <span><i class="iconfont big-icon-test15"></i>{{(item.playCount/10000).toFixed(1)}}万</span>
          </div>
          <span class="remd_text">{{item.name}}</span>
        </div>
      </div>
<!--      最新音乐-->
      <div style="height: 20px;line-height: 20px;padding: 20px 0;">
        <span style="border-left: 2px solid #C20C0C;padding: 2px 0 2px 10px;font-size: 16px;font-weight: 600;">最新音乐</span>
      </div>
      <!--         歌曲显示固定格式-->
      <div class="newSong">
        <div class="newSong_li" v-for="(item, index) in newSongs" :key="index">
          <div class="newSong_li_title ellips" v-html="item.title"></div>
          <div class="newSong_li_author"><i class="big-big-icon-test2 iconfont" style="font-size: 27px;"></i></div>
          <div class="newSong_li_icon ellips"><i class="iconfont big-sq1" style="margin-right: 4px;color: #eea375;"></i>{{item.author}}-{{item.special}}</div>
        </div>
      </div>
    </div>
    <div class="nevaBarPage" v-show="nevaBarId === 2">
      <!--<div class="hotop">
        <div class="hotop-r"></div>
      </div>-->
      <div class="hotop">
        <div class="hotopct">
          <div class="u-hmsprt hoticon"></div>
          <div class="hottime">更新日期：04月18日</div>
        </div>
      </div>
      <div class="newSong">
        <div class="newSong_li" v-for="(item, index) in hotSong" :key="index">
          <div class="newSong_li_img newSong_li_hot" :style="index<3?'color: #df3436;':''">{{index &lt; 9 ? '0' + (index + 1) : index + 1}}</div>
          <div class="newSong_li_title ellips" style="padding-left: 46px;" v-html="item.title"></div>
          <div class="newSong_li_author"><i class="big-big-icon-test2 iconfont" style="font-size: 27px;"></i></div>
          <div class="newSong_li_icon ellips" style="padding-left: 46px;"><i class="iconfont big-sq1" style="margin-right: 4px;color: #eea375;"></i>{{item.author}}-{{item.special}}</div>
        </div>
      </div>
    </div>
    <div class="nevaBarPage" v-show="nevaBarId === 3">
      <search v-model="searchName" :keyEnter="search"></search>
      <div class="search-i" v-if="!showSonglist">
        热门搜索
        <div class="search-hot">
          <span  v-for="(item, index) in hotSearch" :key="index" @click="search(item.first)">{{item.first}}</span>
        </div>
        <div class="search-log" style="margin-top: 6px;">
          <div v-for="(item, index) in searchLog" :key="index" v-if="item" @click="search(item)">
            <i class="iconfont big-jilu" style="font-size: 18px;float: left;margin-right: 10px;"></i>{{item}}<i class="iconfont big-cha1" style="font-size: 16px;float: right;"></i>
          </div>
        </div>
      </div>
      <div class="search-l" v-else>
<!--         歌曲显示固定格式-->
        <div class="newSong">
          <div class="newSong_li" v-for="(item, index) in SongList" :key="index">
            <div class="newSong_li_img" ><img :src="item.pic" style="width: 30px;height: 30px;"></div>
            <div class="newSong_li_title ellips" style="padding-left: 46px;" v-html="item.title"></div>
            <div class="newSong_li_author" style="padding-left: 46px;"><i class="big-big-icon-test2 iconfont" style="font-size: 27px;"></i></div>
            <div class="newSong_li_icon ellips" v-html="item.author+'-'+item.special" ><i class="iconfont big-sq1" style="margin-right: 4px;color: #eea375;"></i></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Search from './search'
export default {
  name: 'HelloWorld',
  components: {Search},
  data () {
    return {
      nevaBarId: 1, // topbar
      searchName: [], // 搜索框内容
      searchLog: [], // 搜索记录
      playList: [], // 歌单
      newSongs: [], // 最新音乐
      hotSearch: [], // 热门搜索词
      showSonglist: false, //  是否显示歌曲列表
      SongList: [], // 歌曲列表
      hotSong: [] // 热门歌曲
    }
  },
  mounted () {
    this.getPersonalized() // 获取推荐歌单
    this.getNewSong() // 获取最新歌曲
    this.getHotSearch() // 获得热搜关键词
    this.searchLog = this.common.getStorage('searchLog') || []
    if (this.searchLog.length > 0) this.searchLog = this.searchLog.split(',')
  },
  methods: {
    // 获得热门歌曲
    getHotSong () {
      this.$get(`${this.domin}/api/top/list?idx=1`, {}).then(response => {
        // alert('成功！')
        console.log(response)
        let data = response.body.result
        let item = []
        for (let i = 0; i < 100; i++) {
          item.push(data[i])
        }
        this.hotSong = item
        // this.hotSearch = response.body.result.hots
      })
    },
    // 获得热门搜索
    getHotSearch () {
      this.$get(`${this.domin}/api/search/hot`, {}, false).then(response => {
        // alert('成功！')
        // console.log(response.body.result)
        this.hotSearch = response.body.result.hots
      })
    },
    // 获得最新音乐
    getNewSong () {
      this.$get(`${this.domin}/api/personalized/newsong`, {}).then(response => {
        // alert('成功！')
        console.log(response.body.result)
        this.newSongs = response.body.result
      })
    },
    // 获得推荐歌单
    getPersonalized () {
      this.$get(`${this.domin}/api/personalized?limit=6`, {}).then(response => {
        // alert('成功！')
        console.log(response)
        this.playList = response.body.result
      })
    },
    // 歌曲搜索
    search (name) {
      if (!name) name = this.searchName
      else this.searchName = name
      if (name === '') return
      console.log(name)
      this.$get(`${this.domin}/api/search?name=${name}`, {}).then(response => {
        // alert('成功！')
        if (!this.searchLog.includes(name)) this.searchLog.push(name)
        this.common.setStorage('searchLog', this.searchLog.join(','))
        // this.poemDetails.content = poemData[0].content.replace(/\|/g, '<br>').replace(new RegExp(this.poem.content, 'g'), "<span style='color: #F56C6C;'>$&</span>")
        this.showSonglist = true
        console.log(response)
        let SongList = response.body.result.songs
        for (let i in SongList) {
          SongList[i].title = SongList[i].title.replace(/\|/g, '<br>').replace(new RegExp(name, 'g'), "<span style='color: #507daf;'>$&</span>")
          SongList[i].special = SongList[i].special.replace(/\|/g, '<br>').replace(new RegExp(name, 'g'), "<span style='color: #507daf;'>$&</span>")
        }
        this.SongList = SongList
      })
    }
  },
  watch: {
    nevaBarId: function (newVal) {
      console.log(newVal)
      if (newVal === 1 && this.newSongs.length < 1) this.getNewSong() // 获取最新歌曲
      else if (newVal === 2 && this.hotSong.length < 1) this.getHotSong() // 获取最新歌曲
      else if (newVal === 3 && this.hotSearch.length < 1) this.getHotSearch() // 获得热搜关键词
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  font-size: 12px;
}
.text{
  font-family: sans-serif;
  font-size: 30px;
  cursor: pointer;
  color: #fff;
}
.action{
  color:#C20C0C;
  border-bottom: 2px #C20C0C solid;
}
.nevaBar{
  z-index: 8;
  background-color: #fff;
  width: 100%;
  height: 40px;
  line-height: 30px;
  font-size: 14px;
  border-bottom: 1px solid #ccc;
  position: fixed;
  top: 40px;
}
.nevaBar div{
  width: 32%;
  display: inline-block;
  text-align: center;
  height: 40px;
  line-height: 40px;
  padding-bottom: 2px;
  box-sizing: border-box;
}
.nevaBar div span{
  display: inline-block;
  height: 100%;
  padding: 0 10px;
  transition:color 300ms;
}
.nevaBarPage{
  position: absolute;
  top: 80px;
  width: 100%;
  overflow: auto;
  background-color: #fbfcfd;
}
.ream-ul{
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.ream-ul .ream-li{
  display: inline-block;
  position: relative;
  width: 33.3%;
  /*float: left;*/
  padding-left: 2px;
  box-sizing: border-box;
  /*padding-right: 2px;*/
}
.ream-ul .ream-li div span{
  position: absolute;
  top: 2px;
  right: 2px;
  color: #fff;
  text-shadow: 1px 0 0 rgba(0,0,0,.15);
}
.ream-ul .remd_text{
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  padding: 6px 2px 8px 6px;
  line-height: 1.2;
  font-size: 13px;
  mergin-top: 0;
}
.ream-ul div img{
  width: 100%;
  height: 100%;
  /*padding-bottom: 100%;!* padding百分比相对父元素宽度计算 *!*/
  /*margin-: 2%;*/
}
.newSong_li{
  position: relative;
  margin-bottom: 8px;
}
.newSong_li_img{
  position: absolute;top: 4px;left: 6px;float: left;display: inline-block;width: 30px;
}
.newSong_li_hot{
  font-size: 18px;
  text-align: center;
  height: 40px;
  line-height: 40px;
}
.newSong_li_title{
  padding-left:6px;font-size: 17px;padding-right: 30px;box-sizing: border-box;
}
.newSong_li_author{
  float: right;height: 100%;position: absolute;top: 0;right: 6px;line-height: 39px;
}
.newSong_li_icon{
  padding-right: 30px;box-sizing: border-box;padding-left:6px;color:#888;font-size: 12px;
}
.search-i{
  color: #333;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  padding: 10px 8px 0 8px;
  font-size: 14px;
}
.search-hot span{
  font-size: 14px;
  display: inline-block;
  border: 1px solid #ccc;
  padding: 6px;
  margin: 6px;
  /* border-radius: 12%; */
  border-radius: 16px;
}
.search-log div{
  width: 100%;
  padding: 6px 0;
  font-size: 16px;
}
.hotop {
  position: relative;
  padding-top: 38.9%;
  overflow: hidden;
  background: url(//s3.music.126.net/m/s/img/hot_music_bg_2x.jpg?f01a252…) no-repeat;
  background-size: contain;
}
.hotopct {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  z-index: 2;
  padding-left: 20px;
  box-sizing: border-box;
}

.hotop:after, .hotopct {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.u-hmsprt {
  background: url(//s3.music.126.net/m/s/img/index_icon_2x.png?5207a28…) no-repeat;
  background-size: 166px 97px;
}
.hoticon {
  width: 142px;
  height: 67px;
  background-position: -24px -30px;
}
.hottime {
  margin-top: 10px;
  color: hsla(0,0%,100%,.8);
  font-size: 12px;
  transform: scale(.91);
  transform-origin: left top;
}
</style>



